.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.div{
  position: absolute;
  top: 50px;
  left: 50px; 
  width: 100px;
  height: 100px;
  background: rgba(30,50,80,1);
  border-radius: 50% 50%;
}

*{
  background-color:beige;
}

.array-container {
  margin: 10% 0% 0% 0%;
  position: center;
}

.array-bar {
  width: 1%;
  height: auto;
  background-color:rgb(244, 184, 73);
  display:inline-block;
  margin: 0% .09% 0% .09%;
}

